export const getOperators = (dataType) =>
{
    if (dataType)
    {
        let operators = [];

        switch (dataType)
        {
            case 2:
            case 4:
            case 5:
                operators = [
                    { id: '=', label: '=' },
                    { id: '>', label: '>' },
                    { id: '<', label: '<' },
                    { id: '>=', label: '>=' },
                    { id: '<=', label: '<=' },
                    { id: 'between', label: 'Gia' }
                ];
                break;
            case 3:
            case 6:
            case 8:
                operators = [
                    { id: '=', label: 'Bng' },
                    { id: 'like', label: 'Ging' },
                    { id: 'is null', label: 'Rng' },
                    { id: 'is not null', label: 'Khng rng' }
                ];
                break;
            case 1:
            case 10:
                operators = [
                    { id: '=', label: 'Bng' },
                    { id: 'like', label: 'Ging' }
                ];
                break;
        }
        return operators;
    }

    return [];
};
